// Here goes the styles for the Everglade app
body {
  background: url("../../uwux-assets/img/backgrounds/ore_bg-dark.png") repeat repeat;
  overflow: hidden;
}

body:after,
body::after {
  content: none;
  background: none;
}

body p,
body li,
body .cell-container .markdown-body p,
body .cell-container .markdown-body li {
  font-family: "Noto Sans";
  line-height: normal;
}

.dropdown-menu {
  height: auto;
  max-height: 75vh;
  overflow-x: hidden;
  top: 1px !important;
  z-index: 10;
  font-size: 1.4rem;

  &::-webkit-scrollbar,
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-track {
    width: 8px;
    border: none;
    background-color: transparent;
  }

  &::-webkit-scrollbar-button,
  &::-webkit-scrollbar-track-piece,
  &::-webkit-scrollbar-corner {
    display: none;
  }

  &::-webkit-scrollbar-track {
    background-color: #4c4c4c;
    -webkit-box-shadow: none;
    box-shadow: none;
    background-repeat: repeat-y;
    background-size: contain;
    margin: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    border-top: 4px solid #ffffff;
    border-bottom: 4px solid #ffffff;
  }
}

.dropdown-item {
  padding-right: 2rem;
}

.nav.nav-tabs.window-tabs {
  position: fixed;
  width: 100%;
  z-index: 100;
  border: none;
  background-color: #b3b1b1;
  height: 5rem;
  padding-top: 4px;
  padding-bottom: 0;
  line-height: 26px;
}
.nav-tabs.dark-tabs .nav-item.active::before {
  border-image-source: url("../../uwux-assets/img/backgrounds-scale9/notchtab-active-corner4-black.png");
  opacity: 0.76;
}
.nav-tabs.dark-tabs .nav-item:hover {
  color: #ffffff;
}

.nav-tabs .nav-item.arrow-nav {
  width: 45px;
}

.nav-tabs .nav-item.arrow-nav::after {
  content: "\f10e";
  font-family: "mc_icons";
  font-size: 20px;
  position: absolute;
  top: 11px;
  transform-origin: center;
  z-index: 9;
  color: #ffffff;
  height: 20px;
  width: 20px;
  //border: 1px solid #FFFFFF;
}
.nav-tabs .nav-item.arrow-nav.disabled::after {
  color: #aaaaaa;
}

.arrow-nav.arrow-nav-left::after {
  transform: rotate(90deg);
  left: 13px;
}

.arrow-nav.arrow-nav-right::after {
  transform: rotate(-90deg);
  left: 10px;
}

.footer_controls {
  position: relative;
  width: 100%;
  background-color: transparent;
  padding: 8px 8px 12px 8px;
  height: 70px;
}

.footer-button {
  display: inline-block;
  position: absolute;
  flex-direction: row;
  align-items: center;
  padding: 5px 10px 5px 5px;
  margin: 5px;
  min-width: 50px;

  .icon {
    font-size: 28px;
    --rotate-offest: 8px;
  }
}

.footer-button.arrow-nav.disabled .icon::before {
  color: #aaaaaa;
}

.footer-button.arrow-nav.arrow-nav-left .icon::before {
  position: absolute;
  transform-origin: center;
  transform: rotate(90deg);
  top: var(--rotate-offest);
  left: 8px;
}

.footer-button.arrow-nav.arrow-nav-right .icon::before {
  position: absolute;
  transform-origin: center;
  transform: rotate(270deg);
  top: var(--rotate-offest);
  left: 11px;
}

.footer-button.arrow-nav.arrow-nav-left {
  left: 8px;
}

.footer-button.arrow-nav.arrow-nav-right {
  right: 8px;
}

#notebook-container {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: visible;
}

.scroll-pane-wrapper {
  position: absolute;
  box-sizing: content-box;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: calc(100vw - 6px);
  border: 3px solid #b3b1b1;
}

.scroll-target {
  position: absolute;
  box-sizing: content-box;
  top: 5rem;
  bottom: 0;
  left: 0;
  right: 0;
  width: calc(100vw - 16px);
  overflow-y: auto;
  overflow-x: hidden;

  &::-webkit-scrollbar,
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-track {
    width: 8px;
    border: none;
    background-color: transparent;
  }

  &::-webkit-scrollbar-button,
  &::-webkit-scrollbar-track-piece,
  &::-webkit-scrollbar-corner {
    display: none;
  }

  &::-webkit-scrollbar-track {
    background-color: #4c4c4c;
    -webkit-box-shadow: none;
    box-shadow: none;
    background-repeat: repeat-y;
    background-size: contain;
    margin: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    border-top: 4px solid #ffffff;
    border-bottom: 4px solid #ffffff;
  }
}

.page-loader {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    content: url("../../uwux-assets/img/loaders/equilizer_loader.gif");
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}

// NOTE: This is a rather hacky and forceful fix for the fact that a number of serialized (i.e. CSS Module) cell classes
// coming with Notebook content have overflow hidden set on at least one axis which messes with having a
// single consistent scroll context.
.scroll-target * {
  overflow: visible;
}
